function CategoryItem({ title, category, value, url, onChange }) {
  return (
    <Box
      className="pointer border-box"
      pr={[10, 0]}
      width={["50%", "25%"]}
      // mr="auto"
      onClick={() => {
        if ((title === "フォトウエディング") | (title === "その他")) {
          onChange({ name: "menu", value: title });
        }
        onChange({ name: "category", value: title });
      }}
    >
      <Box
        className="relative border-box"
        mb={[6, 34]}
        width={["100%", 170]}
        height={["auto", 170]}
        backgroundImage={"url(" + url + ")"}
        backgroundSize="cover"
        backgroundPosition="center"
        pt="100%"
      >
        <MotionDiv
          className="flex items-center justify-center absolute"
          width="100%"
          height="100%"
          top={0}
          backgroundColor="rgba(0, 0, 0, 0.1)"
          initial={{ opacity: 0, display: "none", "--y": "20px" }}
          variants={{
            active: { opacity: 1, "--y": "0px", display: "flex" },
            inactive: {
              opacity: 0,
              "--y": "20px",
              transitionEnd: { display: "none" },
            },
          }}
          animate={category === title ? "active" : "inactive"}
        >
          <CCheckSvg style={{ transform: "translate(0, var(--y))" }} />
        </MotionDiv>
      </Box>

      <Text
        mb={7}
        textShadow={[0.3, "black"]}
        fontFamily="TsukuARdGothic"
        fontSize={[14, 16]}
        color="black"
      >
        {title}
      </Text>
      <Text
        fontFamily="TsukuARdGothic"
        fontSize={[14, 16]}
        mb={[10, 0]}
        color="grey7"
        letterSpacing="2px"
      >
        {value}
      </Text>
    </Box>
  );
}

function RequiredBox(props) {
  return (
    <Box
      as="span"
      backgroundColor={props.required ? "#c82333" : "#218838"}
      px={10}
      py={4}
      borderRadius={20}
      fontFamily="TsukuARdGothic"
      fontSize={12}
      color="white"
      textShadow={[0, "none"]}
      {...props}
    >
      {props.required ? "必須" : "任意"}
    </Box>
  );
}
export default Page1;
