import React, { Component } from "react";
import Page1 from "./Page1";
import Page2 from "./Page2";
import Page3 from "./Page3";
import { MotionDiv, Box, MovePage } from "../styles";
import Header from "../components/Header";
import Footer from "../components/Footer";
const initial = { opacity: 0, display: "none", x: 30 };
const variants = {
  active: { display: "block", opacity: 1, x: 0, transition: { duration: 0.5 } },
  inactive: {
    opacity: 0,
    x: -30,
    transition: { duration: 0.2 },
    transitionEnd: { display: "none", x: 30 },
  },
};

class Reservation extends Component {
  constructor(props) {
    super(props);
    this.state = {
      page: 1,
      prefecture: "",
      address: "",
      mail: "",
      phonenumber: "",
      request: "",
      menu: "",
      category: "",
      hour: "",
      year: "",
      month: "",
      day: "",
      number: "30枚",
      type: ["USB"],
      onClickDay: false,
    };
    this.handleFormInputChanged = this.handleFormInputChanged.bind(this);
    this.allInputed = this.allInputed.bind(this);
    this.to = this.to.bind(this);
  }

  render() {
    return (
      <MovePage id="movepage">
        <Header />
        <MotionDiv
          initial={initial}
          variants={variants}
          animate={this.state.page === 1 ? "active" : "inactive"}
        >
          <Page1
            category={this.state.category}
            menu={this.state.menu}
            number={this.state.number}
            month={this.state.month}
            day={this.state.day}
            type={this.state.type}
            prefecture={this.state.prefecture}
            address={this.state.address}
            phonenumber={this.state.phonenumber}
            mail={this.state.mail}
            request={this.state.request}
            onChange={this.handleFormInputChanged}
            to={this.to}
            page={this.state.page}
          />
        </MotionDiv>
        <MotionDiv
          initial={initial}
          variants={variants}
          animate={this.state.page === 2 ? "active" : "inactive"}
        >
          <Page2
            category={this.state.category}
            menu={this.state.menu}
            number={this.state.number}
            month={this.state.month}
            day={this.state.day}
            type={this.state.type}
            prefecture={this.state.prefecture}
            address={this.state.address}
            phonenumber={this.state.phonenumber}
            mail={this.state.mail}
            request={this.state.request}
            onChange={this.handleFormInputChanged}
            to={this.to}
          />
        </MotionDiv>
        <MotionDiv
          initial={initial}
          variants={variants}
          animate={this.state.page === 3 ? "active" : "inactive"}
        >
          <Page3 />
        </MotionDiv>
        <Footer />
      </MovePage>
    );
  }

  to(page) {
    window.scrollTo({ top: 0, left: 0 });
    this.setState({ page: page });
  }

  handleFormInputChanged(event) {
    if (event.target === undefined) {
      this.setState({ [event.name]: event.value });
    } else {
      this.setState({ [event.target.name]: event.target.value });
    }
  }

  allInputed() {
    let result = true;
    for (let key in this.state) {
      if (this.state[key] === "") {
        result = false;
        break;
      }
    }
    // return result
    return true;
  }
}

export default Reservation;
